import React, { Component } from 'react';

import { Form, Input, Tooltip, Icon, message, Cascader, Select, Row, Col, Checkbox, Button, AutoComplete } from 'antd';
const FormItem = Form.Item;
const { TextArea } = Input;

class RegistrationForm extends React.Component {
  state = {
    confirmDirty: false,
  };

  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        message.info(values.name +' Your Message has been Sent ');
      }
    });
  }
  handleConfirmBlur = (e) => {
    const value = e.target.value;
    this.setState({ confirmDirty: this.state.confirmDirty || !!value });
  }

  render() {
    const { getFieldDecorator } = this.props.form;

    const formItemInput = {
      wrapperCol: {
        xs: { span: 9, offset: 2 },
        md: { span: 9, offset:2 },
      },
    };

    const formItemArea = {
      wrapperCol: {
        xs: { span: 9, offset: 2 },
        md: { span: 9, offset:2 },
      },
    };


    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 2,
        },
        sm: {
          span: 24,
          offset: 2,
        },
      },
    };

    return (
      <Form onSubmit={this.handleSubmit}>

      <Col span="9" offset="2">
      <FormItem
      style={{textAlign: "left"}}
      >
        {getFieldDecorator('name', {
          rules: [{
            required: true, message: 'Please input your Name!',
          }],
        })(
          <Input placeholder="Your Name" />
        )}
      </FormItem>
      </Col>

      <Col span="9" offset="2" >
        <FormItem
        style={{textAlign: "left"}}
        >
          {getFieldDecorator('email', {
            rules: [{
              type: 'email', message: 'The input is not valid E-mail!',
            }, {
              required: true, message: 'Please input your E-mail!',
            }],
          })(
            <Input placeholder="Your E-mail" />
          )}
        </FormItem>
        </Col>

        <Col span="20" offset="2" >
        <FormItem
        style={{textAlign: "left"}}
        >
          {getFieldDecorator('message', {
            rules: [{
              required: true, message: 'Please input your Message!',
            }],
          })(
            <TextArea style={{fontSize: 16}} rows={3} placeholder="Write Your Message" />
          )}
        </FormItem>
        </Col>

          <Col span="6" offset="2" className="bottomborder">
          <Button style={{float: "left", marginBottom: 20}} type="primary" htmlType="submit">SEND</Button>
        </Col>
      </Form>
    );
  }
}

const FormTest = Form.create()(RegistrationForm);
export default FormTest;



